import "leaflet/dist/leaflet.css";
import "./styles.css";

import "proj4leaflet";
import L from "leaflet";

// https://epsg.io/3035
// https://spatialreference.org/ref/epsg/3035/
const crs = new L.Proj.CRS(
  "EPSG:3949",
  "+proj=lcc +lat_1=48.25 +lat_2=49.75 +lat_0=49 +lon_0=3 +x_0=1700000 +y_0=8200000 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs",
  {
    origin: [1643766, 8174056],
    bounds: L.bounds(L.point(1643766, 8174056), L.point(1657579, 8182260)),
    resolutions: [
      16384,
      8192,
      4096,
      2048,
      1024,
      512,
      256,
      128,
      64,
      32,
      16,
      8,
      4,
      2
    ]
  }
);

const map = new L.Map("app", {
  crs: crs,
  worldCopyJump: false,
  // maxBoundsViscosity: 1.0,
  maxBounds: [
    [32.88, -16.1],
    [84.17, 40.18]
  ]
}).setView([45, 12], 3);

// const wmsURL = "http://apps.ecmwf.int/wms/?key=public"
const wmsURL =
  "http://54.36.123.13/qgis/qgis_mapserv.fcgi.exe?SERVICE=WMS&VERSION=1.3.0&SLD_VERSION=1.1.0&REQUEST=GetMap&map=C:/OSGeo4W64/apps/qgis-ltr/bin/P04_P05.qgs&LAYERS=BATITOUT&format=image%2Fpng&transparent=false&width=256&height=256&srs=EPSG%3A3949&bbox=1643766,8174056,1657579,8182260";

L.tileLayer
  .wms(
    wmsURL
    //   {
    //   // crs: crs,
    //   maxZoom: 1,
    //   // minZoom: 1,
    //   layers: "basic:land__resolution_medium,basic:coastlines__resolution_medium",
    //   version: "1.1.0",
    //   format: "image/png",
    //   tiled: true,
    //   tileSize: 256
    // }
  )
  .addTo(map);

L.polyline(
  [
    [32.88, -16.1],
    [84.17, -16.1],
    [84.17, 40.18],
    [32.88, 40.18],
    [32.88, -16.1]
  ],
  { color: "red" }
).addTo(map);
